import { useState, useEffect } from 'react'
import { Q, models } from 'cozy-client'
import { useI18n } from 'cozy-ui/react/I18n'

const { folder } = models
const { MAGIC_FOLDERS, ensureMagicFolder } = folder

const useHomeShortcuts = client => {
  const { t } = useI18n()
  const [files, setFiles] = useState([])

  useEffect(() => {
    const load = async () => {
      const { _id: dirId } = await ensureMagicFolder(
        client,
        MAGIC_FOLDERS.HOME,
        t('magic_folder_path')
      )
      const { data } = await client.query(
        Q('io.cozy.files').where({ dir_id: dirId, class: 'shortcut' })
      )
      setFiles(data)
    }
    load()
  }, [])

  return files
}

export default useHomeShortcuts
