import React from 'react'
import { withClient } from 'cozy-client'
import get from 'lodash/get'
import Icon from 'cozy-ui/react/Icon'
import useFetchShortcut from 'hooks/useFetchShortcut'
import { useBreakpoints } from 'cozy-ui/transpiled/react'

const ShortcutTile = ({ client, file }) => {
  const shortcut = useFetchShortcut(client, file)
  const url = get(shortcut, 'shortcutInfos.data.attributes.url', '#')
  const shortcurtImg = get(file, 'metadata.icon')
  const label = file.name.substr(0, file.name.lastIndexOf('.'))
  const { isMobile } = useBreakpoints()

  return (
    <a href={url} className="item">
      <div className="item-icon">
        {shortcurtImg ? (
          <img
            src={`data:image/svg+xml;base64,${btoa(shortcurtImg)}`}
            width="100%"
            alt=""
          />
        ) : (
          <Icon
            icon="device-browser"
            size={isMobile ? 32 : 40}
            color="var(--charcoalGrey)"
          />
        )}
      </div>
      <h3 className="item-title">{label}</h3>
    </a>
  )
}

export default withClient(ShortcutTile)
